const request = require('common/request');
const userCenter = require('common/models/usercenter');

const wechat = userCenter.getQueryString('/UserUnion/Authorize', {
  utype: 2,
  _referer: (location.href)
});
const qq = userCenter.getQueryString('/UserUnion/Authorize', {
  utype: 1,
  _referer: (location.href)
});
const weibo = userCenter.getQueryString('/UserUnion/Authorize', {
  utype: 3,
  _referer: (location.href)
});

export default {
  props: ['showAlterLogin', 'alterLoginPosition', 'position'],
  data() {
    return {
      currentLoginMethod: 'phone',
      loginError: false,
      showPassword: false,
      extName: G.extName,
      url: location.href,
      account: '',
      password: '',
      phone: '',
      verifyCode: '',
      countDown: 0,
      remember_me: false,
      smsLoginUrl: '',
      wechat: wechat,
      qq: qq,
      weibo: weibo
    }
  },
  methods: {
    goLogin() {
      if (this.position === 'gwd-popup') {
        window.open('https://www.gwdang.com/user/login')
      } else {
        let u = encodeURIComponent(location.href)
        location.href = `https://www.gwdang.com/user/login?ext=1&from_url=${u}`;
      }
    },
    goLink(place) {
      let param = {};
      if (place === 'wechat') {
        param.utype = '2';
      }
      if (place === 'qq') {
        param.utype = '1'
      }
      if (place === 'weibo') {
        param.utype = '3'
      }
      if (this.position === 'gwd-popup') {
        window.open(userCenter.getQueryString('/UserUnion/Authorize', param))
      } else {
        param._referer = location.href
        location.href = (userCenter.getQueryString('/UserUnion/Authorize', param))
      }
    },
    goReg() {
      if (this.position === 'gwd-popup') {
        window.open('https://www.gwdang.com/user/register')
      }
      location.href = 'https://www.gwdang.com/user/register';
    },
    sendSms() {
      if (this.countDown > 0) {
        return
      }
      require('common/models/usercenter').sendSms(this.phone).then(res => {
        this.countDown = 60;
        console.log(res)
        this.smsLoginUrl = res.data.capi;
        let t = setInterval(() => {
          if (this.countDown === 0) {
            clearInterval(t);
            return ;
          }
          this.countDown--;
        }, 1000)
      })
    },
    login() {
      if (this.currentLoginMethod === 'phone') {
        if (!this.smsLoginUrl) {
          alert('');
          return ;
        }
        request.post(`${this.smsLoginUrl}`, {
          code: this.verifyCode
        }, true).then(res => {
          console.log(res)
          if (res.code === 1) {
            document.body.dispatchEvent(new CustomEvent('gwd-login-complete', { detail: res.data }))
          }
        })
      } else {
        require('common/models/usercenter').login(this.account, this.password, this.remember_me).then(res => {
          console.log(res)
          if (res.code === 1) {
            document.body.dispatchEvent(new CustomEvent('gwd-login-complete', { detail: res.data }))
          }
        })
      }
    }
  },

}



