import CommonLogin from './CommonLogin.vue'

export default {
  components: { CommonLogin },
  data() {
    return {
      opened: false
    }
  },
  methods: {
    showBox() {
      this.opened = true
    },
    close() {
      this.opened = false
    }
  }
}



