const template = require('vendor/template')
const util = require('./util')
const user = require('./user')
const countryInfo = require('./getCountry')
const userCenter = require('common/models/usercenter')
let bodyClick = util.bodyClick
var loginState

module.exports.init = (islg) => {
  util.eventLog('collect:track')
  chrome.browserAction.setBadgeText({
    text: ""
  })
  if (islg) {
    loginState = true;
    init2()
  } else {
    checkLogin((isLogin) => {
      if (isLogin) {
        $('.favor-box').empty().hide()
        $('#ext_remind').addClass('loading')
        changeTabState("0")
        getInfo(0, renderCollect)
        addEvent()
      } else {
        renderLoginRemind()
      }
    })
  }
}
module.exports.renderLoginOut = () => {
  loginState = null;
  renderLoginRemind()
}
const init2 = () => {
  $('.favor-box').empty().hide()
  $('#ext_remind').addClass('loading')
  changeTabState("1")
  getInfo(1, renderCollect)
  addEvent()
  console.log('init2 called')
  chrome.runtime.sendMessage({
    type: "clearReduce",
  });
}
const changeTabState = (id) => {
  $('.favor-detail .mini-nav span[data-id="' + id + '"]').addClass('tabnow')
  $('.favor-detail .mini-nav span[data-id="' + id + '"]').addClass('select')
}

const editCollectionPrice = (price, site_id) => {
  /**/
  let money = ''
  let cInfo = countryInfo.init(site_id);
  if (cInfo) {
    money = cInfo[0]
  }
  if (price <= 0) {
    return {
      'price': "",
      'Cclass': 'no_price'
    }
  } else {
    return {
      'price': money + price.toString().replace(/([0-9]{2})$/, '.$1'),
      'Cclass': ''
    }
  }
}

const getCheaper = (col, now, site_id) => {
  let mstr = "";
  let cInfo = countryInfo.init(site_id);
  if (cInfo) {
    mstr = cInfo[0]
  }
  col = Number(col);
  now = Number(now);
  if (!col || !now) return '';
  if (col > now) {
    return '' + mstr + ((col - now) / 100) + '';
  } else {
    return ''
  }
}
const editData = (data, price_down) => {
  let newData = []
  for (let i = 0; i < data.length; i++) {
    let getCheap = getCheaper(data[i].collection_price, data[i].price, data[i].site_id)
    let editC = editCollectionPrice(data[i].price, data[i].site_id);
    data[i].cheaper = getCheap ? '' : ' no_cheaper';
    if (!data[i].reduce && data[i].collection_price > data[i].price) {
      data[i].reduce = (data[i].collection_price - data[i].price) / 100;
    }
    data[i].cheaperPri = getCheap;
    data[i].cClass = editC.Cclass;
    data[i].eprice = editC.price;
    if (price_down == 1 && data[i].reduce) {
      newData.push(data[i])
    }
  }
  if (price_down == 1) {
    return newData;
  } else {
    return data;
  }
}

const renderLoginRemind = () => {
  let html2 = require('raw-loader!../views/noLogin.html')
  $('.favor-box').empty().append(template.compile(html2)()).show()
  $('.no-login-btn').on('click', () => {
    window.open('https://www.gwdang.com/user/login')
  })
}

