const renderCollect = (price_down, data) => {
  if (!data) return;
  let txtObj = {
    'noinfo_1': "~",
    'noinfo_0': "~"
  }
  $('#ext_remind').removeClass('loading')
  if (data.length === 0) {
    let str = "noinfo_" + price_down;
    $('.no-info').addClass(str)
    $('.no-info span').text(txtObj[str])
  } else {
    data = data.map(item => {
      if (!item.img_url) {
        item.img_url = '../images/popup/noImage@2x.png'
      }
      if (item.img_url == 'undefined') {
        item.img_url = '../images/popup/noImage@2x.png'
      }
      return item;
    }).sort(function(a, b) {
      return (a.newreduce? 0: 1) - (b.newreduce? 0: 1)
    })
    console.log('after sort data', data);
    if (price_down == 1) {
      data.sort(function(a, b) {
        if (a.priceDownTime == b.priceDownTime) {
          return (new Date(b.update_time)) - (new Date(a.update_time))
        } else {
          return (b.priceDownTime? b.priceDownTime: 0) - (a.priceDownTime? a.priceDownTime: 0)
        }
      })
    }
    let html = require('raw-loader!../views/collect2.html')
    $('.favor-box').empty().append(template.compile(html)({
      data: data,
      price_down: price_down
    })).show()
    setTimeout(() => {
      $('img').each(function() {
        var p = $(this)[0];
        $(this)[0].onerror = function() {
          p.src = '../images/popup/noImage@2x.png'
        }
        $(this)[0].onload = function() {
          if ($(this)[0].naturalWidth < 10) {
            p.src = '../images/popup/noImage@2x.png'
          }
        }
      })
    }, 0)
    $('body').removeClass('noBtBar')
    bodyClick()
  }
  $('.favor-box')[0].scrollTop = 0;
}
const addEvent = () => {
  $('.favor-detail .mini-nav span').on('click', (e) => {
    if ($(e.target).hasClass('tabnow')) {
      return;
    }
    if (!loginState) return;
    $('body').addClass('noBtBar')
    $('.favor-detail .mini-nav span').removeClass('tabnow')
    $('.favor-detail .mini-nav span').removeClass('select')
    $(e.target).addClass('tabnow')
    $(e.target).addClass('select')
    let id = $(e.target).attr('data-id')
    $('.no-info').removeClass('noinfo_0')
    $('.no-info').removeClass('noinfo_1')
    $('#ext_remind').addClass('loading')
    $('.favor-box').hide()
    util.eventLog('collect:change:' + id)
    getInfo(id, renderCollect)
  })
}


