const template = require('vendor/template')
const util = require('./util')
let bodyClick = util.bodyClick

var totalSize = 0;
var tStart = 0;
let scrollT = 0,
  time1

module.exports.init = () => {
  $('.store-nav .mini-nav span:first-child').addClass('tabnow select')
  getStoreInfo("", render)
  addEvent()
}

const getStoreInfo = (id, callback) => {
  id = id || '';
  let idstr = 'storenav' + id;
  util.getStoreMsg(idstr, function(data) {
    if (data && data[idstr]) {
      callback(data[idstr])
    } else {
      let url = `https://www.gwdang.com/window/market?tab=${id}`
      $.getJSON(url).done(function(data) {
        util.setStoreMsg(idstr, data)
        callback(data)
      })
    }
  })
}

const render = (data) => {
  let html = require('raw-loader!../views/store.html')
  $('.store-box')[0].scrollTop = 0;
  $('.store-box').empty().append(template.compile(html)({
    data: data.data
  }))
  totalSize = data.data.length;
  lazyLoad(0, 32)
  tStart = 32;
  scrollT = 0;
  $('.store-box').off('scroll', scrollFnc)
  $('.store-box').on('scroll', scrollFnc)
  bodyClick()
}

const lazyLoad = (start, end) => {
  end = Math.min(end, totalSize)
  let dom = $('.store-item img');
  for (let i = start; i < end; i++) {
    let src = dom.eq(i).attr('data-original')
    if (!src) continue;
    dom.eq(i).attr('src', src)
    dom.eq(i).attr('data-original', '')
  }
}

const scrollFnc = (e) => {
  clearTimeout(time1);
  time1 = setTimeout(function() {
    let scrollTop = $('.store-box').scrollTop()
    if (scrollTop - 192 >= scrollT) {
      let num = parseInt(scrollTop / 192) + 2;
      lazyLoad(tStart, tStart + num * 4)
      tStart = tStart + num * 4;
    }
  }, 400)
}

const addEvent = () => {
  $('.store-nav .mini-nav span').on('click', (e) => {
    if ($(e.target).hasClass('tabnow')) {
      return;
    }
    $('.store-nav .mini-nav span').removeClass('tabnow')
    $('.store-nav .mini-nav span').removeClass('select')
    $(e.target).addClass('tabnow')
    $(e.target).addClass('select')
    let id = $(e.target).attr('data-id')
    getStoreInfo(id, render)
  })
}


