const util = require('./modules/util')
const user = require('./modules/user')
const storeNav = require('./modules/storeNav')
const collect2 = require('./modules/collect2');
var userLogin = user.userLogin

var popupObj = {}


module.exports.init = () => {
  // cookie 
  util.getCookie((data) => {
    user.getUInfo(function(uname) {
      if (uname) {
        $('body').on('click', () => {
          requestPermissions()
        })
        showUser(uname)
        popupObj["isLogin"] = true;
        if (data) {
          try {
            data = decodeURIComponent(data)
            G.objUser = {
              email: data
            };
          } catch (e) {}
        }
      }
      chrome.storage.local.get('favorList', function(data) {
        if (data.favorList && data.favorList.length > 0 && uname) {
          renderModel("favor-box", true)
          $('.nav span').removeClass('select')
          $('.nav span[data-id="favor-box"]').addClass('select')
        } else {
          renderModel("store-nav")
        }
      })
      require('search').init()
      require('bannerAd').init("https://www.gwdang.com/window/banner")
      addEvent()
    })
  })
}

var hasRequestPermission

function requestPermissions() {
  if (hasRequestPermission) return;
  hasRequestPermission = true;
  chrome.permissions.request({
    permissions: ["notifications"],
  }, function(granted) {});
}

function showUser(u) {
  if (!u) return;
  $('.login-btn').hide();
  $('.username').text(u)
  $('.user-info').show()
}

function renderModel(id, islogin) {
  if (id === 'favor-box') {
    $('.body-detail').hide()
    $('.favor-detail').show()
    minitabStateReset(id)
    collect2.init(islogin)
    $('.more-link').attr('href', "https://www.gwdang.com/mine/collection").text(" >")
    $('body').addClass('noBtBar')
    G.tabFavor = true;
    $('.favor-box')[0].scrollTop = 0
  } else if (id === 'store-nav') {
    $('.body-detail').hide()
    $('.store-nav').show()
    minitabStateReset(id)
    storeNav.init()
    $('.more-link').attr('href', "https://www.gwdang.com/market").text(" >")
    $('body').removeClass('noBtBar')
    G.tabFavor = false;
  } else if (id === 'app-show') {
    minitabStateReset(id)
    $('.body-detail').hide()
    $('.app-show').show()
    $('body').addClass('noBtBar')
    G.tabFavor = false;
  }
}

const minitabStateReset = (id) => {
  console.log('click', id)
  if (id !== "favor-box") {
    $('.favor-box')[0].scrollTop = 0
    $('.favor-detail .mini-nav span').removeClass('tabnow')
    $('.favor-detail .mini-nav span').removeClass('select')
  }
  if (id !== "store-nav") {
    $('.store-nav .mini-nav span').removeClass('tabnow')
    $('.store-nav .mini-nav span').removeClass('select')
  }
}

const addEvent = () => {
  var uUnfold
  // 
  const LoginBox = require('common/components/ModalLogin.vue').default;
  let loginBox = null;
  $('.login-btn span').on('click', function() {
    if (!loginBox) {
      let el = document.createElement('div')
      el.id = 'loginBox'
      document.body.appendChild(el)
      loginBox = new Vue({
        el: '#loginBox',
        ...LoginBox
      })
    }
    console.log(loginBox)
    loginBox.showBox();
  })
  // 
  $('.login_detail_close').on('click', function() {
    $('.close_module').hide()
  })
  $('.setting-mod em').on('click', function() {
    util.eventLog('ext-set:click')
    util.openPage(chrome.extension.getURL("html/setting.html"), true)
  })

  $('.nav').on('click', (e) => {
    let dom = $(e.target)
    let id = dom.attr('data-id');
    if (dom.hasClass('select')) {
      return;
    }
    // 
    if (id === 'ext-set') {
      util.openPage(chrome.extension.getURL("html/setting.html"), true)
      util.eventLog(id + ':change')
      return;
    } else {
      renderModel(id)
    }
    util.eventLog(id + ':change')
    $('.nav span').removeClass('select')
    dom.addClass('select')
  })
  // 
  $('.exit-box').on('click', () => {
    $('.user-info').hide()
    $('.login-btn').show()
    G.objUser = {}
    collect2.renderLoginOut()
    minitabStateReset()
    chrome.storage.local.remove('user_info')
    chrome.runtime.sendMessage({
      type: "loginout",
    });
    util.removeCookie()
    require('common/models/usercenter').logout()
  })

  /**/
  $('.login_click').on('click', function() {
    var email = $('#username').val()
    var password = $('#password').val()
    if (!email || !password) {
      $('#username, #password').val('');
      $('.login_remind').show().fadeOut(3000)
      return;
    }
    userLogin(email, password, () => {
      if (G.tabFavor) {
        collect2.init()
      }
    });
  })
  // 
  $('.user-info').on('click', function() {
    if (uUnfold) {
      $('.user-info').removeClass('unfold')
      $('.exit-box').hide()
      uUnfold = false;
    } else {
      $('.user-info').addClass('unfold')
      $('.exit-box').show()
      uUnfold = true;
    }
  })

  // 
  $('body').on('click', function(e) {
    if ($(e.target).parents('.user-info').length == 0) {
      $('.user-info').removeClass('unfold')
      $('.exit-box').hide()
      uUnfold = false;
    }
  })

  $('.more-link, .popup-logo').on('click', function() {
    let href = $(this).attr('href')
    util.openPage(href, false)
    return false;
  })

  let OpenEye
  $('.passeye').on('click', function() {
    if (OpenEye) {
      $('#password').attr('type', 'password')
      $('.passeye').removeClass('onfocus')
      OpenEye = false;
    } else {
      $('#password').attr('type', 'text')
      $('.passeye').addClass('onfocus')
      OpenEye = true;
    }
  })

}


