const request = require('common/request');
const md5 = require('js-md5');
const host = 'i.gwdang.com';
const userData = require('common/userData')

type Response = {
  code: number
}

let test: Response = {
  code: 1
};

const signParams = params => {
  const query = Object.keys(params).sort((a, b) => {
    return a.localeCompare(b);
  }).map(x => x + (params[x])).join('');
  console.log('query', query)
  let res = md5(md5(query) + params.ac)
  return res;
};

const post = (url, params, data) => {
  params.platform = 'ext';
  params.t = parseInt((new Date()).getTime() / 1000)
  const toSign = {...params, ...data, ac: url.toLowerCase().replace('/', '').replace(/\//g, '.')}
  params.sign = signParams(toSign);
  console.log('sign', params.sign)
  const query = Object.keys(params).map(x => x + '=' + encodeURIComponent(params[x])).join('&');

  return request.post(`https://${host}${url}?${query}`, data, true)
};

const get = (url, params) => {
  params.platform = 'ext';
  if (!params.t) {
    params.t = parseInt((new Date()).getTime() / 1000)
  }
  params.sign = signParams({
    ...params,
    ac: url.toLowerCase().replace('/', '').replace(/\//g, '.')
  });
  const query = Object.keys(params).map(x => x + '=' + encodeURIComponent(params[x])).join('&');
  if (G.allowBackgroundRequest) {
    return request.rawGet(`https://${host}${url}?${query}`)
  } else {
    return new Promise(resolve => {
      $.get(`https://${host}${url}?${query}`).then(res => {
        try {
          let r = JSON.parse(res)
          resolve(r)
        } catch (e) {
          resolve(res)
        }
      })
    })
  }
};

const getQueryString = (url, params) => {
  params.platform = 'ext';
  params.t = parseInt((new Date()).getTime() / 1000)
  params.sign = signParams({
    ...params,
    ac: url.toLowerCase().replace('/', '').replace(/\//g, '.')
  });
  const query = Object.keys(params).map(x => x + '=' + encodeURIComponent(params[x])).join('&');
  return (`https://${host}${url}?${query}`)
};

let current = null;

let currentCollectId = null;

const getDetail = dp_id => {
  return new Promise((resolve, reject) => {
    if (current) {
      resolve(current)
    } else {
      get('/UserCollection/Detail', {
        dp_id: dp_id
      }).then(res => {
        if (typeof res === 'string') {
          res = JSON.parse(res);
        }
        current = res;
        resolve(res)
      })
    }
  })
};

module.exports = {
  /**
   * 
   * @param username
   * @param password
   * @param remember
   * @returns {*}
   */
  login(username, password, remember) {
    return post('/User/Login', {}, {
      account: username,
      passwd: password,
      remember_me: remember
    })
  },
  /**
   * 
   */
  logout() {
    return get('/User/Logout', {})
  },
  /**
   * 
   * @param {string} phone - 
   * @returns Promise
   */
  sendSms(phone) {
    return post('/UserHelper/SmsCode', {}, {
      phone: phone,
      tag: 'login'
    })
  },

  /**
   * 
   * @param dp_id
   * @returns {*}
   */
  checked(dp_id: string) {
    return get('/UserCollection/Checked', {
      dp_id: dp_id
    })
  },

  /**
   * 
   * @param dp_id
   */
  detail(dp_id: string) {
    return getDetail(dp_id)
  },

  /**
   * 
   * @param tab
   * @param pg
   * @param ps
   * @returns {*}
   */
  getList(tab = 'default', pg: number,  ps: number = 30) {
    return get('/UserCollection/List', {ps: ps, tab: tab})
  },
  /**
   * 
   * @param settingPrice
   * @param type
   * @returns {*}
   */
  add(settingPrice, type) {
    if (current && current.data) {
      return post('/UserCollection/Modify', {}, {
        collection_id: current.data.id,
        notifier_site: type,
        notifier_threshold: settingPrice
      })
    }
    let price = G.dp.price;
    let oinfo = userData.get('other_info')
    if (!price) {
      if (oinfo && oinfo['code-server'] && oinfo['code-server']['price']) {
        price = oinfo['code-server']['price']
      }
    }
    return post('/UserCollection/Add', {}, {
      dp_id: G.dp.itemId,
      title: G.dp.name ? G.dp.name: document.title,
      url: location.href,
      img: G.dp.img ? G.dp.img: '',
      price: price,
      promo_price: 0,
      notifier_site: type,
      notifier_threshold: settingPrice
    })
  },
  /**
   * 
   * @param id
   * @returns {*}
   */
  delete(id) {
    return post('/UserCollection/Delete', {}, {
      collection_ids: id
    })
  },
  checkLoginStatus() {
    return post('/User/Detail', {}, {})
  },
  /**
   * 
   * @param time
   */
  checkFavor(time) {
    return get('/UserCollection/Tip', {
      t: time / 1000
    })
  },
  getQueryString
};


