/**/
var localst = chrome.storage.local;

function eventLog(event) {
  if (!event) event = ""
  var n = 'img_random' + Math.random()
  var img = window[n] = new Image()
  img.onload = img.onerror = function() {
    window[n] = null
  }
  img.src = 'https://browser.gwdang.com/visit/?from_device=chrome&random=' + Math.random() + '&action=' + event;
}
module.exports.setStoreMsg = (key, msg) => {
  var obj = {};
  obj[key] = msg;
  localst.set(obj)
}
module.exports.getStoreMsg = (key, callback) => {
  localst.get(key, function(info) {
    callback(info)
  })
}
const openPage = (url, active) => {
  active = active || false;
  chrome.tabs.create({
    url: url,
    active: active
  })
}
module.exports.openPage = openPage
module.exports.ImgLoadCheck = (img_url, id) => {
  var obj = document.getElementById(id);
  var img = new Image();
  img.src = img_url;
  img.onerror = function() {
    obj.src = "https://s1.gwdang.com/images/extensions/product_img_default.jpg";
  }
}
module.exports.bodyClick = () => {
  $('.body-content').off();
  /**/
  $('.body-content').on('click', function(e) {
    if (e.target.nodeName === 'A') {
      let id = $(e.target).attr('data-id')
      eventLog(id)
      openPage(e.target.href)
      return false;
    } else if (e.target.parentNode.nodeName === 'A') {
      let id = $(e.target.parentNode).attr('data-id')
      eventLog(id)
      openPage(e.target.parentNode.href)
      return false;
    }

  })
}
module.exports.productClick = () => {
  $('.gwd_body').off();
  /**/
  $('.gwd_body').on('click', function(e) {
    if (e.target.nodeName === 'A') {
      openPage(e.target.href)
    } else if (e.target.parentNode.nodeName === 'A') {
      openPage(e.target.parentNode.href)
    }
    var txt1 = $('.gwd_nav1 .select').text();
    var txt2 = $('.gwd_nav2 .select').text();
    if (txt1 === '') {
      eventLog('click:winpopup:collect:product')
    } else {
      var name = $('.today_re .select').attr('name');
      eventLog('click:winpopup:' + name + ':product')
    }
    return false;
  })
}
module.exports.eventLog = eventLog;

module.exports.getCookie = (callback) => {
  chrome.cookies.get({
    url: "https://www.gwdang.com/",
    name: "plt_user_email"
  }, function(e) {
    if (e && e.value) {
      callback(e.value)
    } else {
      callback()
    }
  })
}
module.exports.removeCookie = (callback) => {
  chrome.cookies.remove({
    url: "https://www.gwdang.com/",
    name: "plt_user_email"
  }, function(e) {
    if (callback)
      callback(e)
  })
}


