const util = require('./util')
module.exports.userLogin = (email, password, callback) => {
  var obj = {
    email: email,
    password: password,
    t: 'window'
  };
  var xml = new XMLHttpRequest();
  xml.addEventListener('load', function() {
    if (xml.response && xml.response.result_code == 0) {
      $('#username, #password').val('');
      $('.login_remind').show().fadeOut(3000)
    }
    if (xml.response && xml.response.result_code == 1) {
      $('.login-btn').hide();
      $('.username').text(xml.response.user.nickname)
      $('.user-info').show()
      $('.close_module').hide();
      var obj = {
        name: xml.response.user.nickname,
        email: xml.response.user.email,
        id: xml.response.user.id
      };
      G.objUser = obj;
      util.setStoreMsg('user_info', obj);
      if (callback) {
        callback()
      }
      chrome.runtime.sendMessage({
        type: "loginRender",
      });
    }
  })
  xml.open('POST', 'https://www.gwdang.com/user/aj_login')
  xml.responseType = "json";
  xml.setRequestHeader("Content-Type", "application/x-www-form-urlencoded");
  xml.send('email=' + email + '&password=' + password + '&t=window');
}

module.exports.getUInfo = (callback) => {
  let url = `https://www.gwdang.com/user/info`;
  // $.getJSON(url).done((data) => {
  //   if (data.code === 0 && data.data.nickname) {
  //     callback(data.data.nickname)
  //   } else {
  //     callback()
  //   }
  // }).fail(() => {
  //   callback()
  // })
  require('common/models/usercenter').checkLoginStatus().then(res => {
    res = JSON.parse(res)
    console.log(res)
    if (res.data) {
      callback(res.data.nickname)
    } else {
      callback()
    }
  })
}


