define('userData', function() {
  var exports = {}
  var data = {}
  exports.get = function(key) {
    return data[key]
  }
  exports.data = data
  exports.set = function(key, value) {
    if (value === undefined && key) {
      data = key
    }else {
      data[key] = value
    }
  }
  return exports
})


