const util = require('./util')

module.exports.init = () => {
  addEvent()
}

const addEvent = () => {
  var showSearch;
  // 
  $('body').on('click', function(e) {
    console.log('len:',$(e.target).parents('.select-btn').length)
    if (!$(e.target).parents('.select-btn').length) {
      console.log(e.target)
      $('.select_div').hide()
      $('.select-btn').removeClass('showselect')
      showSearch = false;
    }
  });
  $('.select-btn').on('click', function() {
    if (!showSearch) {
      $('.select_div').show()
      $('.select-btn').addClass('showselect')
      showSearch = true;
    } else {
      $('.select_div').hide()
      $('.select-btn').removeClass('showselect')
      showSearch = false;
    }
  })
  $('#subBtm').on('click', () => {
    let keyword = $('#search_input').val()
    let site = $('.select-btn>span').text()
    if (!keyword) return;
    searchSet(site, keyword)
  })
  $('#search_input').on('keyup', function(e) {
    if (e.keyCode === 13) {
      let keyword = $('#search_input').val()
      let site = $('.select-btn>span').text()
      if (!keyword) return;
      searchSet(site, keyword)
    }
  })
  $('.select_div').on('click', (e) => {
    if (e.target.nodeName === "SPAN") {
      let txt = $(e.target).text()
      if (txt) {
        $('.select-btn>span').text(txt)
        $('.select_div').hide()
        showSearch = false;
      }
    }
  })
}

const searchSet = (site, keyword) => {
  if (!keyword) return;
  var vipK = "&ju=JUU2JTg4JTkxJUU2JTk4JUFGdmlwc2hvcA";
  var patterns = {
    '': {
      site_id: 3,
      pattern: "https://search.jd.com/Search?enc=utf-8&keyword="
    },
    '': {
      site_id: 1,
      pattern: "https://www.amazon.cn/s?field-keywords="
    },
    '': {
      site_id: 2,
      pattern: "http://searchb.dangdang.com/?key="
    },
    '': {
      site_id: 25,
      pattern: "https://search.suning.com/"
    },
    '': {
      site_id: 83,
      pattern: "https://s.taobao.com/search?q="
    },
    '': {
      site_id: 28,
      pattern: "https://www.gome.com.cn/search?question="
    },
    '': {
      site_id: 31,
      pattern: "https://search.yhd.com/s2/c0-0/k"
    },
    '': {
      site_id: 129,
      pattern: "https://www.gwdang.com/search?from=ext&crc64=1&s_vip="
    },
    '': {
      site_id: '',
      pattern: "https://www.gwdang.com/search?from=ext&s_product="
    }
  }
  var searchApi = patterns[site]['pattern'];
  var site_id = patterns[site]['site_id'];
  var u_server = '';
  if (site === '') {
    u_server = searchApi + encodeURIComponent(keyword);
  } else if (site === '') {
    u_server = searchApi + encodeURIComponent(keyword);
  } else if (site === '') {
    u_server = searchApi + encodeURIComponent(keyword) + '/';
  } else if (site === '') {
    u_server = searchApi + encodeURIComponent(keyword) + '&ju=JUU2JTg4JTkxJUU2JTk4JUFGdmlwc2hvcA';
  } else {
    u_server = 'https://u.gwdang.com/union/go/?s_product=' + encodeURIComponent(keyword) + '&target_url=' + encodeURIComponent(searchApi + encodeURI(keyword)) + "&site_id=" + site_id;
  }
  util.eventLog('winpopup:searchClick:' + site_id)
  window.open(u_server);
}


