const template = require('vendor/template')
const util = require('./util')
let jumpUrl

const addEvent = () => {
  // banner
  $('.banner-close-btn').on('click', function() {
    $('.ad-banner').remove();
    $('.body-content').removeClass('hasbanner')
    localStorage.setItem('jumpUrl', jumpUrl)
    util.setStoreMsg('jumpUrl_time', new Date().getTime());
    util.eventLog('bannerClose')
    return false;
  })
  $('.ad-banner img').on('click', function() {
    util.eventLog('bannerClick')
  })

}

module.exports.init = (url) => {
  util.getStoreMsg('jumpUrl_time', function(data) {
    if (data && data.jumpUrl_time) {
      let nowTime = new Date().getTime()
      if (nowTime - data.jumpUrl_time >= 3600 * 1000 * 24 * 7) {
        localStorage.removeItem('jumpUrl')
      }
    }
  });
  $.getJSON(url).done(data => {
    if (data.to_url && data.img_url) {
      if (data.to_url === localStorage.getItem('jumpUrl')) return;
      let img = new Image()
      jumpUrl = data.to_url;
      img.onload = function() {
        $('.ad-banner img').attr('src', data.img_url)
        $('.ad-banner img').attr('srcset', data.img_url.replace('.png', '@2x.png 2x'))
        $('.ad-banner').attr('href', data.to_url).show()
        $('.body-content').addClass('hasbanner')
        addEvent()
      }
      img.src = data.img_url;
    }
  })
}


